@use 'src/template.scss' as *;

header {
    position: relative;
    background-color: $secondary-color;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    text-align: center;
    box-shadow: 0 2px 5px black;
    height: 40px;
}

h1 {
    margin: 0;
    position: absolute;
}

input[type=text] {
    margin-block: 10px;
    width: 300px;
    padding: 8px;
    border-radius: 50px;

    &:focus{
        outline: none;
        &::placeholder {
        visibility: hidden;
    }
    }
}

span {
    color: $third-color;
}

.login-container {
    margin-left: auto
}

a {
    text-decoration: none;
    color: inherit;
}