@use 'src/template.scss' as *;

main {
    text-align: center;
    margin: 0px auto;
    max-width: 1000px;
    
    .login-container {
        height: 90dvh;
        @include flex-vertical;
    justify-content: center;
    }

    .login {
        vertical-align: middle;
        font-size: 70px;
        height: auto;
        width: auto;
        padding: 20px 40px;
        font-weight: 700;
        border-radius: 100px;
        animation: pulse 2s infinite;
    }

    @keyframes pulse {
        0% {box-shadow: 2px 2px 5px black}
        50% {box-shadow: 10px 10px black;
            }
        100% {box-shadow: 2px 2px 5px black}
    }
}

.container {
    display: flex;
    justify-content: space-between;
    margin: 10px;
    height: 700px;
}

@media screen and (max-width: 700px){
    .container {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
    }
}

h4 {
    font-size: 32px;
}