@use 'src/template.scss' as *;

@mixin bg-color {
    background-color: rgba(0, 0, 0, 0.606)
}

@mixin width {
   width: clamp(100px, 48vw, 470px);
   margin-inline: 5px
}

.playlist-container {
    @include bg-color;
    padding-block: 20px;
    @include width;
    
    input[type=text] {
        color: white;
        background-color: rgba(0, 0, 0, 0);
        border: none;
        border-bottom: 2px solid white;
        border-radius: 0;
        width: 80%;
        margin-bottom: 10px;
        font-size: 20px;
    }
}

@media screen and (max-width: 700px){
    .playlist-container {
        margin-top: 10px;
        width: clamp(400px, 96vw, 500px);
    }
}

.playlist > ul {
    position: relative;
    list-style: none;
    text-align: left;
    padding-left: 0;
    display: flex;
    flex-direction: column;
    margin: 10px 10px 30px 10px;
    overflow-y: scroll;
    max-height:510px;

    p {
    color: lightgray;
    }
}