@use 'src/template.scss' as *;
@use 'src/components/Playlist/playlist.scss' as *;

.results {
    @include bg-color;
    @include width;
    position: relative;
    padding-block: 20px;
}

@media screen and (max-width: 700px){
    .results {
        width: clamp(400px, 96vw, 500px);
        height: 350px;
    }
}