@use 'src/template.scss' as *;

li {
    border: none;
    border-bottom: white solid 1px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 5px;
    width: 100%;
    box-sizing: border-box;

    .play {
        border: 2px solid white;
        border-radius: 100px;
        width: 35px;
        height: 35px;
        @include flex-vertical;
        justify-content: center;
        position: absolute;

        &:hover {
            cursor: pointer;
        }
    }

    .animate {
        animation: playpulse .4s infinite;
    }

    @keyframes playpulse {
        50% {color: grey;
            border: 2px solid grey;
        }
    }

    p {
        margin: 0
    }

    .track {
        @include flex-vertical;
        align-items: flex-start;
        width: clamp(80px, 72%, 400px);
    }
    
    .search-item {
        margin-left: 50px;
    }
    
    h3 {
        margin: 0;
    }

    .circle {
        right: 0;
        position: absolute;
        border-radius: 50px;
        width:40px;
        height: 40px;
        @include flex-vertical;
        justify-content: center;

        &:hover {
            cursor: pointer;
        }

        &:active {
            border: 1px solid white;
        }

    }
}