@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap');

$prime-color: #262f61;
$secondary-color: #1a2144;
$third-color: rgb(193, 120, 213);
$third-hover: rgb(150, 93, 166);

@mixin border {
    border: 1px solid black;
}

@mixin flex-vertical {
    display: flex;
    flex-direction: column;
    align-items: center;

}

body {
    font-family: 'Lato', sans-serif;
    background-image: url(images/pxfuel.jpg);
    color: white;
    margin: 0 auto;
}

button {
    padding-inline: 15px;
    height: 38px;
    border-radius: 50px;
    color: white;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.493);
    background-color: $third-color;
    border: none;
    &:hover {
        background-color: $third-hover;
        cursor: pointer;
    }
}

